<template>
<div>
  <div class="content">
    <div class="input-area">
      <form action="post" id="metology-form">
        <div class="panel ">
          <div class="panel-heading">
            <h3 class="panel-title">基本資料</h3>
          </div>
          <div class="panel-body row" id="input-group-a">
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">性別</span><input class="form-control" id="zgender" v-model="A.zgender"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">年齡</span><input class="form-control" id="zage" v-model="A.zage"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">身高</span><input class="form-control" id="aheight" v-model="A.aheight"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">體重</span><input class="form-control" id="aweight" v-model="A.aweight"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">收縮壓</span><input class="form-control" id="astrsbp" v-model="A.astrsbp"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">舒張壓</span><input class="form-control" id="astrdbp" v-model="A.astrdbp"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">體脂率</span><input class="form-control" id="abodyfat" v-model="A.abodyfat"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">腰圍</span><input class="form-control" id="awdth" v-model="A.awdth"></div></div>
          </div>
        </div>

        <div class="panel panel-second">
          <div class="panel-heading">
            <h3 class="panel-title">生活習慣</h3>
          </div>
          <div class="panel-body row" id="input-group-other">
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">喝酒習慣</span><input class="form-control" id="b10dr" v-model="Other.b10dr"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">工作強度</span><input class="form-control" id="c16wk" v-model="Other.c16wk"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">運動與否</span><input class="form-control" id="c19sport" v-model="Other.c19sport"></div></div>
          </div>
        </div>

        <div class="panel ">
          <div class="panel-heading">
            <h3 class="panel-title">血液檢查</h3>
          </div>
          <div class="panel-body row" id="input-group-b">
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">血色素</span><input class="form-control" id="bhb" v-model="B.bhb"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">血球容積</span><input class="form-control" id="bhct" v-model="B.bhct"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">白血球</span><input class="form-control" id="bwbc" v-model="B.bwbc"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">血小板</span><input class="form-control" id="bplt" v-model="B.bplt"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">空腹血糖</span><input class="form-control" id="cacsugar" v-model="B.cacsugar"></div></div>
          </div>
        </div>

        <div class="panel panel-second">
          <div class="panel-heading">
            <h3 class="panel-title">肝功能</h3>
          </div>
          <div class="panel-body row" id="input-group-d">
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">膽紅素總量</span><input class="form-control" id="dtbil" v-model="D.dtbil"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">直接膽紅素</span><input class="form-control" id="ddbil" v-model="D.ddbil"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">鹼性磷酸酶</span><input class="form-control" id="dalp" v-model="D.dalp"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">GOT</span><input class="form-control" id="dsgot" v-model="D.dsgot"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">GPT</span><input class="form-control" id="dsgpt" v-model="D.dsgpt"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">r-胺基酸轉移酶</span><input class="form-control" id="drgt" v-model="D.drgt"></div></div>
          </div>
        </div>

        <div class="panel">
          <div class="panel-heading">
            <h3 class="panel-title">尿液檢查</h3>
          </div>
          <div class="panel-body row" id="input-group-f">
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">尿素氮</span><input class="form-control" id="fbun" v-model="F.fbun"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">肌酸酐</span><input class="form-control" id="fcre" v-model="F.fcre"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">尿酸</span><input class="form-control" id="fua" v-model="F.fua"></div></div>
          </div>
        </div>

        <div class="panel panel-second">
          <div class="panel-heading">
            <h3 class="panel-title">血脂肪</h3>
          </div>
          <div class="panel-body row" id="input-group-g">
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">三酸甘油脂</span><input class="form-control" id="gtg" v-model="G.gtg"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">總膽固醇</span><input class="form-control" id="gchol" v-model="G.gchol"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">甲型胎兒蛋白</span><input class="form-control" id="jafp" v-model="G.jafp"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">C反應蛋白</span><input class="form-control" id="lcrp" v-model="G.lcrp"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">HDL-C</span><input class="form-control" id="ghdlc" v-model="G.ghdlc"></div></div>
            <div class="col-md-4 one-input"><div class="input-group"><span class="input-group-addon">LDL-C</span><input class="form-control" id="gldlc" v-model="G.gldlc"></div></div>
          </div>
        </div>

      </form>
    </div>
    <div class="sidebar">

      <div class="logo"><img src="../assets/metology-icon.png"/></div>
      <p class="text-sm-center HDataPred">HDataPred</p>

      <ul class="list-group">
        <li :class="{ 'list-group-item': true, 'finished': AFinish }">
          基本資料
          <span v-if="AFinish === false" class="float-xs-right"><i class="fa fa-times fa-fw" aria-hidden="true"></i></span>
          <span v-else class="float-xs-right"><i class="fa fa-check-square-o fa-fw" aria-hidden="true"></i></span>
        </li>

        <li :class="{ 'list-group-item': true, 'finished': OtherFinish }">
          生活習慣
          <span v-if="OtherFinish === false" class="float-xs-right"><i class="fa fa-times fa-fw" aria-hidden="true"></i></span>
          <span v-else class="float-xs-right"><i class="fa fa-check-square-o fa-fw" aria-hidden="true"></i></span>
        </li>

        <li :class="{ 'list-group-item': true, 'finished': BFinish }">
          血液檢查
          <span v-if="BFinish === false" class="float-xs-right"><i class="fa fa-times fa-fw" aria-hidden="true"></i></span>
          <span v-else class="float-xs-right"><i class="fa fa-check-square-o fa-fw" aria-hidden="true"></i></span>
        </li>

        <li :class="{ 'list-group-item': true, 'finished': DFinish }">
          肝功能
          <span v-if="DFinish === false" class="float-xs-right"><i class="fa fa-times fa-fw" aria-hidden="true"></i></span>
          <span v-else class="float-xs-right"><i class="fa fa-check-square-o fa-fw" aria-hidden="true"></i></span>
        </li>

        <li :class="{ 'list-group-item': true, 'finished': FFinish }">
          尿液檢查
          <span v-if="FFinish === false" class="float-xs-right"><i class="fa fa-times fa-fw" aria-hidden="true"></i></span>
          <span v-else class="float-xs-right"><i class="fa fa-check-square-o fa-fw" aria-hidden="true"></i></span>
        </li>

        <li :class="{ 'list-group-item': true, 'finished': GFinish }">
          血脂肪
          <span v-if="GFinish === false" class="float-xs-right"><i class="fa fa-times fa-fw" aria-hidden="true"></i></span>
          <span v-else class="float-xs-right"><i class="fa fa-check-square-o fa-fw" aria-hidden="true"></i></span>
        </li>
      </ul>
      <button class="btn btn-default" type="button" data-toggle="modal" data-target=".bs-example-modal-lg" @click="defaultValue">帶入預設數值</button>
      <button v-bind:disabled="allFinished" :class="{'btn': true, 'btn-predict': true, 'success': success }" type="button" data-toggle="modal" data-target=".bs-example-modal-lg" @click="predict">預測風險 <i v-if="running" class="fa fa-spinner fa-spin fa-fw"></i><i v-if="success" class="fa fa-check-square-o fa-fw" aria-hidden="true"></i></button>
    </div>
  </div>
  <result :showResult="showResult" :risk="risk" v-on:closeResult="closeResult"></result>
</div>
</template>

<script type="text/javascript">
import axios from 'axios'
import Result from './Result'

export default {
  name: 'HDataPred',
  data () {
    return {
      A: {
        zgender: '',
        zage: '',
        aheight: '',
        aweight: '',
        astrsbp: '',
        astrdbp: '',
        abodyfat: '',
        awdth: ''
      },
      Other: {
        b10dr: '',
        c16wk: '',
        c19sport: ''
      },
      B: {
        bhb: '',
        bhct: '',
        bwbc: '',
        bplt: '',
        cacsugar: ''
      },
      D: {
        dtbil: '',
        ddbil: '',
        dalp: '',
        dsgot: '',
        dsgpt: '',
        drgt: ''
      },
      F: {
        fbun: '',
        fcre: '',
        fua: ''
      },
      G: {
        gtg: '',
        gchol: '',
        jafp: '',
        lcrp: '',
        ghdlc: '',
        gldlc: ''
      },
      running: false,
      success: false,
      showResult: false,
      risk: ''
    }
  },
  components: {
    Result
  },
  methods: {
    defaultValue () {
      this.A.zgender = 2
      this.A.zage = 67
      this.A.aheight = 153
      this.A.aweight = 46.2
      this.A.astrsbp = 109
      this.A.astrdbp = 52
      this.A.abodyfat = 22.1
      this.A.awdth = 68

      this.Other.b10dr = 1
      this.Other.c16wk = 2
      this.Other.c19sport = 5

      this.B.bhb = 13.9
      this.B.bhct = 40.6
      this.B.bwbc = 5.1
      this.B.bplt = 265
      this.B.cacsugar = 118

      this.D.dtbil = 0.68
      this.D.ddbil = 0.14
      this.D.dalp = 200
      this.D.dsgot = 17
      this.D.dsgpt = 17
      this.D.drgt = 36

      this.F.fbun = 15.1
      this.F.fcre = 0.7
      this.F.fua = 5

      this.G.gtg = 120
      this.G.gchol = 261
      this.G.jafp = 6
      this.G.lcrp = 0.2
      this.G.ghdlc = 53
      this.G.gldlc = 184
    },
    predict () {
      const instance = axios.create()
      const creds = {'conditions': [
        this.A.zgender,
        this.A.zage,
        this.A.aheight,
        this.A.aweight,
        this.A.astrsbp,
        this.A.astrdbp,
        this.B.bhb,
        this.B.bhct,
        this.B.bwbc,
        this.B.bplt,
        this.B.cacsugar,
        this.D.dtbil,
        this.D.ddbil,
        this.D.dalp,
        this.D.dsgot,
        this.D.dsgpt,
        this.D.drgt,
        this.F.fbun,
        this.F.fcre,
        this.F.fua,
        this.G.gtg,
        this.G.gchol,
        this.G.jafp,
        this.G.lcrp,
        this.G.ghdlc,
        this.G.gldlc,
        this.A.abodyfat,
        this.A.awdth,
        this.Other.b10dr,
        this.Other.c16wk,
        this.Other.c19sport
      ]}

      // set the running icon
      this.running = true

      instance.post('//metology.herokuapp.com/api/predict/diabetes/', creds)
        .then((response) => {
          this.risk = response.data
          this.running = false
          this.success = true
          this.showResult = true
        })
        .catch(() => {
          console.log('failed')
        })
    },
    closeResult () {
      this.showResult = false
    }
  },
  computed: {
    AFinish () {
      return checkFinished(this.A)
    },
    OtherFinish () {
      return checkFinished(this.Other)
    },
    BFinish () {
      return checkFinished(this.B)
    },
    DFinish () {
      return checkFinished(this.D)
    },
    FFinish () {
      return checkFinished(this.F)
    },
    GFinish () {
      return checkFinished(this.G)
    },
    allFinished () {
      if (this.AFinish && this.OtherFinish && this.BFinish && this.DFinish && this.FFinish && this.GFinish) return false
      return true
    }
  }
}

function checkFinished (item) {
  let isFinish = 0
  let keys = 0
  for (const key in item) {
    if (item[key] !== '') isFinish += 1
    keys += 1
  }
  if (isFinish === keys) return true
  return false
}

</script>

<style type="text/css" lang="sass" scoped>
@import url('https://fonts.googleapis.com/css?family=Proza+Libre');
$color1: rgb(17, 88, 168)
$color2: rgb(153,206,147)
$color3: rgb(101,189,188)
$color4: #6699A1
$color5: #EB7A77

#metology-form
  padding-bottom: 30px;

.panel
  display: block
  margin: 2% 0
  border: 1px solid $color4
  border-radius: 10px
  .panel-heading
    background-color: $color4
    padding: 5px 10px
    color: white
    display: flex
    border-radius: 10px 10px 0 0
    .panel-title
      font-size: 20px
      margin: auto
      padding: 5px 0
  .row
    padding: 10px 10px
    .col-sm-4
      padding: 3%

.panel.panel-second
  border-color:  $color5
  .panel-heading
    background-color: $color5


.input-group
  padding-top: 3%

.app
  height: 100%
  width: 100%

.content
  position: relative
  width: 100%
  height: calc(100vh - 56px)

  .input-area
    padding: 10px 6%
    position: absolute
    width: calc(100% - 280px)
    height: 100vh
    overflow-y: auto
    &::-webkit-scrollbar
      display: none

  .sidebar
    position: absolute
    width: 280px
    left: calc(100% - 280px)
    padding: 20px 40px
    height: 100vh
    background-color: $color1
    border-left: 1px solid gray
    .logo
      display: flex
      width: 200px
      height: 200px
      border-radius: 50%
      background-color: white
      img
        margin: auto
        width: 90%

    .HDataPred
      font-family: 'Proza Libre', serif;
      font-size: 30px
      color: white
      padding: 20px

    .list-group
      width: 200px
      box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);
      color: $color5


  .btn-predict
    position: absolute;
    width: 200px;
    bottom: 20px;
    background-color: $color5
    border-color: $color5
    color: white
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);
    transition: all 0.3s ease;
 

  .btn-default
    position: absolute;
    margin-top: 10px;
    width: 200px;
    background: transparent;
    border: 1px solid white;
    color: white;

  .finished
    color: $color4

  .success
    background-color: $color4
    border-color: $color4

</style>




