import Vue from 'vue'
import VueRouter from 'vue-router'
import Metology from './Metology'
import HdataPred from './components/HDataPred'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    { path: '/', component: Metology },
    { path: '/HDataPred', component: HdataPred }
  ]
})

/* eslint-disable no-new */
new Vue({
  router,
  template: `
  <div id="app">

      <router-view ></router-view>
      
  </div>
  `
}).$mount('#app')
