<template>
<transition name="fade">
<div class="modal-base" v-show="showResult">
  <div class="modal">
    <div class="container">
      <div class="close-result" @click="closeResult">&times;</div>
      <div class="result-wrap">
        <h3 class="text-sm-center">
          風險預測結果
        </h3>
        <div class="row">
          <div class="col-sm-3">
            <canvas id="diabetes" width="100%"></canvas>
            <p class="text-sm-center risk-text">糖尿病風險: {{ this.riskShow }} %</p>
          </div>
          <div class="col-sm-3">
            <canvas id="disease2" width="100%"></canvas>
            <p class="text-sm-center risk-text">高血壓風險: 32 %</p>
            </div>
          <div class="col-sm-3">
            <canvas id="disease3" width="100%"></canvas>
            <p class="text-sm-center risk-text">高血脂風險: 88 %</p>
            </div>
          <div class="col-sm-3">
            <canvas id="disease4" width="100%"></canvas>
            <p class="text-sm-center risk-text">心血管疾病風險: 54 %</p>
          </div>
        </div>
      </div>
      <div class="suggestion-wrap">
        <h3 class="text-sm-center">給您的建議</h3>
        <p class="text-sm-center">{{ suggestion }}</p>
        <suggestion v-if="showSuggestion"></suggestion>
      </div>
    </div>
  </div>
</div>
</transition>
</template>

<script>
import Chart from 'chart.js'
import Suggestion from './Suggestion'

export default {
  props: ['showResult', 'risk'],
  name: 'Result',
  components: {
    Suggestion
  },
  data () {
    return {
      plotData: {
        datasets: [{
          data: '',
          backgroundColor: [
            '#1158a8',
            'rgb(224,224,224)'
          ]
        }]
      },
      suggestion: '您在糖尿病的風險沒有很高，請繼續保持良好的生活習慣！',
      showSuggestion: false,
      riskShow: ''
    }
  },
  methods: {
    closeResult () {
      this.$emit('closeResult')
    }
  },
  watch: {
    risk () {
      const riskRound = Math.round(this.risk * 1000) / 1000
      console.log(riskRound)
      this.riskShow = 100 * riskRound
      this.plotData.datasets[0].data = [this.riskShow, 100 - this.riskShow]
      if (this.riskShow > 0.5) this.showSuggestion = true; this.suggestion = ''
      console.log('risk: ', this.riskShow)

      // diabetes
      const diabetes = document.getElementById('diabetes')
      this.chart = new Chart(diabetes, {
        type: 'doughnut',
        data: this.plotData,
        options: {
          hover: {
            intersect: false
          },
          tooltips: {
            enabled: false
          }
        }
      })

      // disease2
      const disease2 = document.getElementById('disease2')
      this.chart = new Chart(disease2, {
        type: 'doughnut',
        data: {
          datasets: [{
            data: [32, 68],
            backgroundColor: [
              '#F7C242',
              'rgb(224,224,224)'
            ]
          }]
        },
        options: {
          hover: {
            intersect: false
          },
          tooltips: {
            enabled: false
          }
        }
      })

      // disease3
      const disease3 = document.getElementById('disease3')
      this.chart = new Chart(disease3, {
        type: 'doughnut',
        data: {
          datasets: [{
            data: [12, 88],
            backgroundColor: [
              '#90B44B',
              'rgb(224,224,224)'
            ]
          }]
        },
        options: {
          hover: {
            intersect: false
          },
          tooltips: {
            enabled: false
          }
        }
      })
      // disease4
      const disease4 = document.getElementById('disease4')
      this.chart = new Chart(disease4, {
        type: 'doughnut',
        data: {
          datasets: [{
            data: [54, 46],
            backgroundColor: [
              '#E87A90',
              'rgb(224,224,224)'
            ]
          }]
        },
        options: {
          hover: {
            intersect: false
          },
          tooltips: {
            enabled: false
          }
        }
      })
    }
  }
}
</script>

<style lang="sass">
.modal-base
  z-index: 10
  position: fixed
  height: 100%
  width: 100%
  background-color: rgba(0,0,0, 0.5)
  top: 0
  display: flex
  .modal
    margin: auto
    width: 80%
    height: 80%
    background-color: white
    border-radius: 20px
    display: block
    position: relative
    .close-result
      position: absolute;
      right: 30px;
      top: 10px;
      font-size: 30px;
      z-index: 12
      transition: color 0.3s ease
      &:hover
        color: rgb(224,224,224)


    .result-wrap
      padding-top: 40px;
      h3
        padding-bottom: 10px

    .suggestion-wrap
      padding-top: 40px
      h3
        padding-bottom: 10px

.risk-text
  padding-top: 10px
  </style>



